@import "./abstracts/vars";
@import "./abstracts/mixins";

*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body {
    font-family: 'Montserrat', sans-serif;
}

.container {
    padding: 0 15px;
    @include media(xs) {
        padding: 0 35px;
    }
    @include media(sm) {
        padding: 0 50px;
    }
    @include media(md) {
        padding: 0;
        margin: 0 auto;
        width: 1140px;
    }
}

@import "./header";
@import "./main-block";